.todoListMain .header input {
    padding: 10px;
    font-size: 1rem;
    border: 2px solid #FFF;
    width: 20rem;
}

.todoListMain .header button {
    padding: 10px;
    font-size: 1rem;
    margin: 10px;
    background-color: rgb(67, 0, 250);
    border: 2px solid #FFF;
    width: 102px;
}

.todoListMain .header button:hover {
    background-color: yellow;
    border: 2px solid #caccca;
    cursor: pointer;
}

li {
    font-size: 21px;
    font-family: Arial, Helvetica, sans-serif;
    margin: 1rem;
    margin-left: 20vw;
    margin-right: 20vw;
    padding: 2em;
    border-left: .7rem solid rgb(74, 58, 219);
    border-top: 0.5rem solid white;
    border-right: 0.5rem solid white;
    border-bottom: 0.5rem solid white;
    background-color: #dde7ec8f;
}

li:hover {
    cursor: pointer;
    background-color: yellow;
}

.todoListMain {
    border: 2px black;
}

.todoListMain .header {
    background-color: #deeaee;
    background-image: linear-gradient( #46388F, rgb(202, 221, 214));
}

.todoListMain {
    background-color: rgb(229, 229, 233);
}

.Thedate {
    display: inline-block;
    margin-left: 0%;
    margin-right: 88%;
    font-size: 1 em;
    color: rgb(238, 107, 84);
}

h1 {
    margin-right: 9%;
}

.alternative {
    color: #1f9ad3;
    border-radius: 1%;
    border: 2px solid white;
    display: block;
    padding: 30rem;
    margin: 5%;
}